<template>
    <div class="agency-tjt">
      <agency-under-cell v-for="cellData in agencyTJTArr" :cell-data="cellData" :is-replace="false" class="agency-under-cell"></agency-under-cell>
      <div v-show="showEmpty&&(!agencyTJTArr||agencyTJTArr.length==0)" class="empty-container">
        <empty-bg :msg="emptyMsg"></empty-bg>
      </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import agencyUnderCell from "../../../public/agencyUnderCell.vue";
import EmptyBg from "../../../popupBox/emptyBg.vue";
import bus from '../../../../common/js/eventBus.js';

export default {
  name: "agency-tjt",
  data() {
    return {
      emptyMsg: "您还未添加美导~",
      showEmpty:false
    };
  },
  components: {
      agencyUnderCell,
      EmptyBg
  },
  mounted() {
    bus.$on("cancel-refresh-upper-data", () => {
      if(this.agencyTJTArr.length==0) this.showEmpty=true;
    });
    if(this.agencyTJTArr&&this.agencyTJTArr.length!=0){
      return;
    } 
    this.requestTjtData();
  },
  computed: {
    ...mapState(["userid","openid", "path","agencyTJTArr"])
  },
  methods: {
    requestTjtData(){
    this.$http
      .post(this.path + "php/Route.php", {
        type:'showagentlist',
        aid:this.userid,
        roletp:4,
        page:0
      })
      .then(response => {
        let resultData = response.body.arr;
        if(resultData&&resultData.length!=0) this.showEmpty=false;
        else this.showEmpty=true;
        this.$store.commit("UPDATA_A_TJT", resultData);
      })
      .catch(response=>{
        //请求出错处理
        console.log(response);
      });
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.agency-tjt{
  width 100%;
  .agency-under-cell{
    width: 100%;
    margin-bottom 5px;
    background-color: #fff;
  }
  .empty-container{
    width 100%;
    height 100%;
  }
}
</style>



